import { fetchProdukWithFoto } from "@/app/lib/data";
import { alegreya, nosifer } from "@/app/ui/fonts";
import MenuClient from "@/app/verif/menu/menu-client";
import Link from "next/link";
import Image from "next/image";
import { useState } from "react";


const Navbar = () => null;
const ShoppingCart = ({ size }: { size: number }) => <span>ðŸ›’</span>;
const UserRound = ({ size }: { size: number }) => <span>ðŸ‘¤</span>;

function useMenuPageState() {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [isCartOpen, setIsCartOpen] = useState(false);
  const [profileOpen, setProfileOpen] = useState(false);

  const toggleDropdown = () => setDropdownOpen((open) => !open);
  const handleLogout = () => {
    // Implement logout logic here
    alert("Logout clicked!");
  };

  return {
    dropdownOpen,
    setDropdownOpen,
    isCartOpen,
    setIsCartOpen,
    profileOpen,
    setProfileOpen,
    toggleDropdown,
    handleLogout,
  };
}

export default function MenuPageWrapper() {
  // This wrapper is needed because hooks can't be used in async components
  return <MenuPage />;
}

function MenuPage() {
  const [produkList, setProdukList] = useState<any[]>([]);
  const [error, setError] = useState<string | null>(null);
  const {
    dropdownOpen,
    setDropdownOpen,
    isCartOpen,
    setIsCartOpen,
    profileOpen,
    setProfileOpen,
    toggleDropdown,
    handleLogout,
  } = useMenuPageState();

  React.useEffect(() => {
    fetchProdukWithFoto()
      .then(setProdukList)
      .catch((err) => {
        console.error('Error fetching products:', err);
        setError('Gagal memuat menu dari database. Silakan coba lagi.');
      });
  }, []);

  try {
    produkList = await fetchProdukWithFoto();
  } catch (err) {
    console.error('Error fetching products:', err);
    error = 'Gagal memuat menu dari database. Silakan coba lagi.';
  }

  if (error) {
    return (
      <main className="min-h-screen bg-gradient-to-r from-red-950 to-black text-white">
      <nav className="flex justify-between items-center p-6 text-[#FFF8E8] ml-7">
        <Link href="/">
          <Image
            src="/logo.png"
            alt="Bloody Espresso Logo"
            width={80}
            height={50}
          />
        </Link>

        <ul
          className={`${alegreya.className} flex gap-5 text-2xl font-bold items-center`}
        >
          <li className="relative mr-10 cursor-pointer hover:text-[#E3CDA2]">
            <Link href="/verif/home">Beranda</Link>
          </li>

          {/* Tentang Kami */}
          <li className="relative mr-16 cursor-pointer hover:text-[#E3CDA2]">
            <span onClick={toggleDropdown} className="pb-1 select-none">
              Tentang Kami
            </span>

            {dropdownOpen && (
              <div className="absolute left-0 mt-2 w-60 bg-gray-800 rounded-lg shadow-lg transition-opacity duration-300 ease-in-out z-10">
                <ul className="py-4 text-lg text-white">
                  <li className="px-6 py-3 hover:bg-gray-700 cursor-pointer">
                    <Link href="/verif/profile/team">Profil Team</Link>
                  </li>
                  <li className="px-6 py-3 hover:bg-gray-700 cursor-pointer">
                    <Link href="/verif/profile/toko">Profil Toko</Link>
                  </li>
                </ul>
              </div>
            )}
          </li>

          {/* Kontak */}
          <li className="relative mr-16 cursor-pointer hover:text-[#E3CDA2]">
            <Link href="/verif/kontak">Kontak</Link>
          </li>

          <li className="relative mr-10 cursor-pointer hover:text-[#E3CDA2]">
            <Link href="/verif/menu">Menu</Link>
          </li>

          <li className="bg-[#7b1e1e] px-6 py-2 rounded-l-full text-center flex space-x-6">
            <div
              className="flex flex-col items-center cursor-pointer"
              onClick={() => setIsCartOpen(true)}
            >
              <ShoppingCart size={40} />
              <span className="text-xl">Keranjang</span>
            </div>
            <div className="relative flex flex-col items-center cursor-pointer">
              <div
                onClick={() => setProfileOpen(!profileOpen)}
                className="flex flex-col items-center"
              >
                <UserRound size={40} />
                <span className="text-xl">Profile</span>
              </div>

              {profileOpen && (
                <div className="absolute top-full mt-2 bg-gray-800 rounded-lg shadow-lg w-40 z-20">
                  <ul className="py-2 text-white text-lg">
                    <li className="px-4 py-2 hover:bg-gray-700 cursor-pointer">
                      <Link
                        href="/verif/akun"
                        onClick={() => setProfileOpen(false)}
                      >
                        Akun Saya
                      </Link>
                    </li>
                    <li
                      className="px-4 py-2 hover:bg-gray-700 cursor-pointer"
                      onClick={() => {
                        handleLogout();
                        setProfileOpen(false);
                      }}
                    >
                      Logout
                    </li>
                  </ul>
                </div>
              )}
            </div>
          </li>
        </ul>
      </nav>        <div className="flex items-center justify-center py-20">
          <div className="text-center">
            <p className={`${alegreya.className} text-xl text-red-300 mb-4`}>{error}</p>
            <form>
              <button 
                type="button"
                onClick={() => window.location.reload()}
                className="bg-[#9b684c] text-white py-2 px-6 rounded-full hover:bg-[#7d543d] transition-colors"
              >
                Coba Lagi
              </button>
            </form>
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-gradient-to-r from-red-950 to-black text-white">
      <Navbar />
      
      <section className="text-center py-20 px-4">
        <h1 className={`${nosifer.className} text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-extrabold tracking-wide text-[#F8E4BE] mb-6`}>
          MENU
        </h1>
        
        <p className={`${alegreya.className} text-lg sm:text-xl md:text-2xl text-[#f5deb3]/80 mb-12 max-w-4xl mx-auto`}>
          Jelajahi semua rasa kopi bersama kami. Selalu ada secangkir kopi baru
          yang layak dicoba.
        </p>

        {produkList.length === 0 ? (
          <p className={`${alegreya.className} text-xl text-[#f5deb3]/60`}>
            Belum ada menu tersedia di database.
          </p>
        ) : (
          <MenuClient produkList={produkList} />
        )}
      </section>
    </main>
  );
}

